<template>
	<div class="gCCamp">
		<gCShikigami v-for="(item,index) in ss"></gCShikigami>
	</div>
</template>

<script>
	import gCShikigami from './GCShikigami'
	
	export default{
		data(){
			return{
				ss:[1,2,3,4,5]
			}
		},
		components:{
			gCShikigami
		}
	}
</script>

<style>
	.gCCamp{
		width: 40.5vw;
		height: 13.5vw;
		margin: auto;
		outline: #cfa5b1 2px solid;
		display: flex;
	}
</style>



